html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, ::after, ::before {
  box-sizing: inherit;
}

body {
  max-width: 700px;
  margin: 0 auto;
  background-image: url("money.jpg");
  background-position: 50%;
  background-size: cover;
  padding: 0 20px;
  color: #eee;
  font-family: "Roboto", sans-serif;
  text-align: center;
}